.work {
    margin: 0 40px;
    display: flex;
    flex-direction: column;
    background-color: #E9EAEA;
    padding: 8rem 0;
    gap: 4rem;

    .work-header {
        display: flex;
        width: 70%;
        margin: auto;
        justify-content: flex-start;
        align-items: flex-end;
        gap: 2rem;

        .work-title {
            font-family: var(--font);
            font-weight: 800;
            font-size: 96px;
            line-height: 115px;
            color: var(--black);

            span {
                color: var(--primary);
            }
        }

        .work-subtitle {
            font-family: 'Lato';
            font-weight: 800;
            font-size: 24px;
            line-height: 29px;
            color: var(--black);

            span {
                font-weight: 400;
                font-style: italic;
                font-family: var(--font-italic);
            }
        }

        .work-paragraph {
            font-family: 'Lato';
            font-weight: 400;
            font-size: 20px;
            line-height: 150%;
            color: var(--black);
            width: 40%;
        }
    }

    .work-content {
        display: flex;
        width: 90%;
        margin: auto;
    }
}