.border-top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--black5);
    padding: 0 40px;
    z-index: 2;

    .border-top-logo {
        font-family: var(--font);
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
    }

    .border-top-link {
        display: flex;
        gap: 2rem;

        .border-top-link-item {
            img {
                width: 80%;
            }
        }
    }
}

.border-left {
    position: fixed;
    top: 0;
    left: 0;
    width: 40px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--black5);
    transition: 0.3s;

    .border-left-menu {
        transform: rotate(-90deg);
        font-family: var(--font);
        font-weight: 700;
        font-size: 12px;
        line-height: 14px;
        cursor: pointer;
    }
}

.border-right {
    position: fixed;
    top: 0;
    right: 0;
    width: 40px;
    height: 100vh;
    background: var(--black5);
}

.border-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 40px;
    width: 100%;
    background: var(--black5);
}

.float-button {
    position: fixed;
    top: 35px;
    right: 35px;
    display: none;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background: var(--black5);
    border-radius: 50%;
    transition: 0.3s;

    .float-button-toggle {
        width: 50%;
        height: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
        gap: 5px;

        span {
            background: var(--black);
            width: 100%;
            height: 2px;
            transition: 0.3s;
            transform-origin: center;
            border-radius: 10px;
        }

        span:nth-child(2) {
            width: 80%;
        }

        span:nth-child(3) {
            width: 90%;
        }
    }

}

.float-button.open {
    .float-button-toggle {
        gap: 0px;

        span:nth-child(1) {
            transform: rotate(-45deg);
        }

        span:nth-child(2) {
            transform: translateX(-50%);
            width: 100%;
            opacity: 0;
        }

        span:nth-child(3) {
            width: 100%;
            transform: rotate(45deg);
        }

    }
}

.sidebar {
    position: fixed;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 4rem;
    height: 100vh;
    width: 80%;
    background: var(--black5);
    transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);

    .sidebar-menu {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        padding: 0 10%;
        gap: 2rem;

        .sidebar-menu-item {
            font-family: var(--font);
            font-weight: 700;
            font-size: 32px;
            line-height: 38px;
        }
    }

    .sidebar-link {
        display: flex;
        padding: 0 10%;
        gap: 2rem;
    }
}

.border-left:hover {
    width: 120px;

    .border-left-menu {
        padding: 40px 0;

        span {
            transform: translateX(10%);
        }
    }

}

.border-top,
.border-bottom,
.border-left,
.border-right {
    animation: showBorder;
    animation-duration: 1s;
}

@keyframes showBorder {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@media (max-width:768px) {

    .border-top,
    .border-bottom,
    .border-left,
    .border-right {
        display: none;
    }

    .float-button {
        display: flex;
    }
}