    .intro-hello {
        display: flex;
        justify-content: center;
        align-items: center;
        background: var(--black);
        height: 100vh;
    }

    .intro-text {
        width: 100%;
        background: var(--black);
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 8rem 0;

        .intro-text-wrapper {
            width: 50%;
            display: flex;
            flex-direction: column;
            gap: 20px;

            .intro-text-title {
                font-family: var(--font);
                font-weight: 400;
                font-size: 52px;
                line-height: 62px;
                color: var(--black5);

                span {
                    font-family: var(--font-italic);
                    font-style: italic;
                }
            }

            .intro-text-link {
                font-family: var(--font-italic);
                font-style: italic;
                font-weight: 400;
                font-size: 16px;
                line-height: 150%;
                color: var(--black5);
                text-decoration-line: underline;
            }

            .intro-text-paragraph {
                font-family: var(--font);
                font-weight: 400;
                font-size: 20px;
                line-height: 150%;
                color: var(--black5);
            }
        }
    }


    @media (max-width:768px) {
        img {
            width: 50%;
        }

        .intro-text {

            .intro-text-wrapper {
                width: 80%;

                .intro-text-title {
                    font-size: 36px;
                    line-height: 42px;
                }

                .intro-text-link {
                    font-size: 14px;
                    line-height: 21px;
                }

                .intro-text-paragraph {
                    font-size: 14px;
                    line-height: 21px;
                }

            }
        }
    }