.step {
    width: 70%;
    margin: auto;
    display: flex;
    flex-direction: column;

    .step-one {
        display: flex;
        margin-top: 8rem;
        margin-bottom: 4rem;
        align-items: flex-start;

        .step-one-text {
            display: flex;
            flex-direction: column;
            gap: 20px;
            flex: 1;

            .step-one-title {
                font-family: var(--font);
                font-weight: 800;
                font-size: 96px;
                line-height: 115px;
                color: var(--black);
                margin-bottom: 45px;

                span {
                    color: var(--primary);
                }
            }

            .step-one-subtitle {
                font-family: 'Lato';
                font-weight: 800;
                font-size: 24px;
                line-height: 29px;
                color: var(--black);
                margin-bottom: 26px;

                span {
                    font-weight: 400;
                    font-style: italic;
                    font-family: var(--font-italic);
                }
            }

            .step-one-paragraph {
                font-family: 'Lato';
                font-weight: 400;
                font-size: 20px;
                line-height: 150%;
                color: var(--black);
            }
        }

        .step-one-img {
            display: flex;
            align-self: flex-end;

            img {
                width: 100%;
            }
        }
    }

    .step-two {
        display: flex;
        margin: 4rem 0;
        align-items: flex-start;

        .step-two-left {
            display: flex;
            flex: 1;
            flex-direction: column;

            .step-two-title {
                font-family: var(--font);
                font-weight: 800;
                font-size: 96px;
                line-height: 115px;
                color: var(--black);

                span {
                    color: var(--primary);
                }
            }

            img {
                display: flex;
                width: 100%;
            }
        }

        .step-two-right {
            display: flex;
            flex-direction: column;
            flex: 1;
            margin-top: 6rem;
            gap: 4rem;

            .step-two-subtitle {
                font-family: var(--font);
                font-weight: 800;
                font-size: 24px;
                line-height: 29px;

                span {
                    font-family: var(--font-italic);
                    font-style: italic;
                    font-weight: 400;
                }
            }

            .step-two-paragraph {
                font-family: var(--font);
                font-weight: 400;
                font-size: 20px;
                line-height: 150%;
            }
        }
    }

    .step-three {
        display: flex;
        flex-direction: column;
        margin-top: 4rem;
        margin-bottom: 8rem;

        .step-three-start {
            display: flex;
            flex-direction: column;
            gap: 4rem;
            margin-bottom: 4rem;

            .step-three-title {
                font-family: var(--font);
                font-weight: 800;
                font-size: 96px;
                line-height: 115px;
                color: var(--black);

                span {
                    color: var(--primary);
                }
            }

            img {
                width: 100%;
            }

        }

        .step-three-end {
            display: flex;
            align-items: flex-start;
            justify-content: center;
            gap: 4rem;

            .step-three-subtitle {
                font-family: var(--font);
                font-weight: 800;
                font-size: 24px;
                line-height: 29px;
                width: 30%;

                span {
                    font-family: var(--font-italic);
                    font-style: italic;
                    font-weight: 400;
                }
            }

            .step-three-paragraph {
                font-family: var(--font);
                font-weight: 400;
                font-size: 20px;
                line-height: 150%;
                width: 50%;
            }
        }
    }
}

@media (max-width:768px) {
    .step {
        .step-one {
            flex-direction: column;

            .step-one-text {

                .step-one-title {
                    font-size: 48px;
                    line-height: 58px;
                }

                .step-one-subtitle {
                    font-size: 16px;
                    line-height: 19px;

                }

                .step-one-paragraph {
                    font-size: 14px;
                    line-height: 150%;
                }
            }
        }

        .step-two {
            flex-direction: column;

            .step-two-left {

                .step-two-title {
                    font-size: 48px;
                    line-height: 58px;
                }
            }

            .step-two-right {
                .step-two-subtitle {
                    font-size: 16px;
                    line-height: 19px;
                }

                .step-two-paragraph {
                    font-size: 14px;
                    line-height: 150%;
                }
            }
        }

        .step-three {
            .step-three-start {
                .step-three-title {
                    font-size: 48px;
                    line-height: 58px;
                }
            }

            .step-three-end {
                flex-direction: column;

                .step-three-subtitle {
                    width: 100%;
                    font-size: 16px;
                    line-height: 19px;

                }

                .step-three-paragraph {
                    width: 100%;
                    font-size: 14px;
                    line-height: 150%;
                }
            }
        }
    }
}